import React, { useState, useEffect } from 'react';
import './App.css';
import Grid from './Components/Grid'
import GridForm from './Components/GridForm'
import ArrowGrid from './Components/ArrowGrid'
import AllWords from './Components/AllWords'
import axios from 'axios'

import Jumbotron from 'react-bootstrap/Jumbotron';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'

function App() {
  const [letters, setLetters] = useState(null)
  const [directions, setDirections] = useState([])
  const [currWord, setCurrWord] = useState(null)
  const [currIdx, setCurrIdx] = useState(0)
  const [size, setSize] = useState(4)
  const [allDirections, setAllDirections] = useState([])

  const url = 'https://stevenyao.pythonanywhere.com/solve_this/'
  //const url = 'http://127.0.0.1:8000/solve_this/'
  useEffect(() => {
    if(letters != null) {
      axios.post(url, {
        "letters": letters,
        "size": size
      })
      .then(response => {
        setAllDirections(response.data.answer)
      }, (error) => {
        console.log(error);
      });
    }
    setCurrIdx(0)
  }, [url, letters])

  useEffect(() => {
    if(letters != null && allDirections != []) {
      setDirections(allDirections[currIdx][1])
      setCurrWord(allDirections[currIdx][0])
      setCurrIdx(currIdx+1)
      // document.getElementsByName("next")[0].style.display = 'block';
    }
  }, [allDirections])


  const handleClick = (data) => {
    setLetters(data)
  }
  const handleNext = () => {
    if(letters != null) {
      setDirections(allDirections[currIdx][1])
      setCurrWord(allDirections[currIdx][0])
      setCurrIdx(currIdx+1)
    }
  }

  return (
    <div className="bg-light">
      <Navbar bg="dark" variant="dark">
        <Navbar.Brand href="#home" className='pl-3'>
          Word Hunt Solver
        </Navbar.Brand>
      </Navbar>
      

      <Container className='p-3 bg-white'>
      <Jumbotron>
        <h1>Never Lose WordHunt Again!</h1>
        <p>
          Simply enter the 16 letters of your word hunt board and watch as the solutions magically appear. Check out my YouTube for more quality content:
        </p>
        <Button href="https://www.youtube.com/channel/UCZDp6CvSxKBEKycW4dpHIJQ" variant="dark" size='lg'>Go to YouTube</Button>
      </Jumbotron>

      <Row>
        <Col md='6'>
          <h1 className='text-center'>
            Enter your letters
          </h1>
          <GridForm onClick = {handleClick} nextClick = {handleNext} currWord= {currWord} size = {size}/>
        </Col>

        <Col md='6'>
          {/* <form className="text-center p-3 next-form" name = "next">
            <p>current word: </p> <h3> {currWord} </h3>
            <br/>
            <Button variant="dark" onClick={() => handleNext()}>Next Word!</Button>
            <p>Click once and then use SPACE or ENTER</p>
          </form> */}
          
          <div className="parent px-1">
            <Grid  letters = {letters} />
            <div className="child">
              <ArrowGrid directions = {directions} size = {size}/>
            </div>
          </div>
          
        </Col>
      </Row>
      <div className='mt-3'>
        <AllWords words={allDirections}/>
      </div>
      
      
    </Container>
    <footer className="bg-dark text-light text-center text-xs p-3 absolute bottom-0 w-full to-bottom">
            &copy; Copyright Steven Yao 2020
    </footer>
    </div>
    
  );
}

export default App;
